@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.footer {
  z-index: 10;
  position: relative;
  padding: 50px 100px 40px 100px;
  display: flex;
  background: $white;

  .logotype {
    a {
      svg {
        width: 50px;
        height: 50px;

        & * {
          fill: $black;
        }
      }
    }
  }

  .contacts {
    margin-left: 50px;

    .text-subheading {
      margin-bottom: 10px;
    }

    a {
      @include transition;

      &:hover {
        opacity: 0.4;
      }
    }
  }

  .menu {
    margin-left: 100px;

    ul {
      display: flex;
      flex-direction: column;

      li {
        &:not(:first-child) {
          margin-top: 10px;
        }
      }

      a {
        @include transition;

        &:hover {
          opacity: 0.4;
        }
      }
    }
  }

  .aside-block {
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .scroll-trigger {
      margin-bottom: 50px;
      @include transition;

      &:hover {
        cursor: pointer;
        opacity: 0.4;
      }
    }

    .social-links {
      display: flex;
      margin-bottom: 40px;
      
      li {
        &:not(:first-child) {
          margin-left: 20px;
        }

        a {
          @include transition;

          &:hover {
            opacity: 0.4;
          }
        }
      }
    }
  }
}